.info img {
	margin-top: 20px;
	height: 220px;
}
.info {
	margin-top: 30px;
	text-align: center;
}
.info-en p, .info-zh p {
	font-size: 15px;
	text-align: left;
	margin-bottom: 15px;
}

.info_pic {
	width: 60px;
	padding-right: 0;
	position: relative;
	top: 10px;
	left: 100px;
}
.info-en {
	width: 220px;
	position: relative;
	top: -195px;
	left: 175px;
}
.info-zh {
	width: 220px;
	position: relative;
	top: -193px;
	left: 165px;
}
.info-zh p{
	padding-top: 2px;
	font-size: 18px;
}
.info_pic img {
	height: 50px;
	width: 50px;
	display: block;
}

@media (max-width: 1210px) {
	.info_pic {
		left: 80px;
	}
	.info-en{
		left: 150px;
		width: 210px;
	}
	.info-zh {
		left: 150px;
		width: 180px;
	}
}

@media (max-width: 1120px) {
	.info_pic {
		left: 60px;
	}
	.info_pic img {
		margin-bottom: 30px;
	}
	.info-en {
		left: 130px;
		top: -235px;
		width: 200px;
	}
	.info-zh {
		left: 120px;
		top: -230px;
		width: 180px;
		margin-bottom: 25px;
	}
	.info-zh p {
		margin-bottom: 25px;
	}
}
@media (max-width: 1020px) {
	.info_pic {
		left: 40px;
	}
	.info-en, .info-zh {
		left: 110px;
	}
	.info-zh p{
		font-size: 17px;
	}
}

@media (max-width: 930px) { /*mobile mode*/

}