.navbar-item, .navbar-item-zh {
	display: inline-block;
	font-size: 24px;
	width: 16%;
  	color: black;
}
.navbar-item img, .navbar-item-zh img {
	height: 100px;
	width: auto;
	min-width: 50px;
	margin-top: 5px;
}

.navbar-item:hover, .navbar-item-zh:hover {
  cursor: pointer;
}

.navbar-click span{
  padding: 5px 20px 5px 20px;
  border-radius: 100px;
  background-color: rgb(120,0,0);
  color: white;
}

.title-en {

}

.title-zh {
	font-size: 52px;
	padding: 10px 0 10px 0;
}


.dropdown_all {
	width: 90%;
	height: 170px;
	margin-left: auto;
	margin-right: auto;
	clear: both;
}

@media (max-width: 1120px) {
	.dropdown_all {
		width: 100%;
	}
}