@import url(https://fonts.googleapis.com/earlyaccess/cwtexfangsong.css);
@import url(https://fonts.googleapis.com/earlyaccess/cwtexyen.css);

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "cwTeXFangSong", serif, helvetica, sans-serif;

}
html {
  width: 100%;
  height: 100vh;
}
.App-header * {
  font-family: "Pangolin", 'cwTeXYen', "ChalkboardSE-Light", "Comic Sans MS", "Comic Sans", cursive, sans-serif;
}

.App {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-image: url('./background/background.png');
  background-size: 100% 100%;
  background-position: center top;
  background-repeat: no-repeat;
  overflow: hidden;
  height: 800px;
}

#chPage {
  display: none;
}

p, ol {
  margin: 12px 0;
  padding: 0;
  font-size: 19px;
}

h3 {
  font-size: 28px;
}

li {
  margin-bottom: 8px;
}


.App-header {
  padding: 0;
  width: 100%;
  min-height: 10vh;
  display: inline-block;
  text-align: center;
  flex-direction: column;
  justify-content: center;
  font-size: 22px;
  color: rgb(120,0,0);
  position: fixed;
  top: 0;
  z-index: 50;
}
#dropdown {
  height: 230px;
}

.dropdown_mobile, #dropdown_list_mobile, #dropdown_detail_mobile {
  display: none;
}

#dropdown_list_mobile {
  background-color: rgb(120,0,0);
  opacity: 0.9;
  color: white;
  width: 100%;
  margin: 0;
}

.dropdown_mobile p {
  margin: 0 !important; 
}

#dropdown_list_mobile li{
  opacity: 0.9;
  font-size: 18px;
}

.title {
  margin: 10px 0 0 0;
  font-size: 42px;
  height: 60px;
  vertical-align: text-bottom;
}
.App-link {
  color: #61dafb;
}

#seperation_line {
    width: 80%;
    margin-left: 10%;
    margin-right: 10%;
    clear: both;
    position: fixed;
    top: 230px;
  }

.content {
  position: fixed;
  top: 250px;
  z-index: 3;
  height: 95vh;
  overflow-y: auto;
  overflow-x: hidden; 
  font-size: 18px;
  width: 100%;
}

.navbar-item img {
  margin-left: auto;
  margin-right: auto;
  clear: both;
}
.column {
  vertical-align: text-top;
}
* .extended {
  padding-bottom: 300px;
}
.big {
  width: 48%;
  margin-right: 10%;
  text-align: left;
}

.small {
  width: 30%;
}
.left {
  clear: both;
  float: left;
  margin-left: 10%;
}
.right {
  margin-left: 0;
  float: right;
}

.photo {
  max-width: 400px;
  max-height: 400px;
  margin-left: -100px;
  margin-top: 20px;
}

.footer {
  height: 40px;
  width: 100%;
  position: fixed;
  bottom: 0;
  text-align: center;
  color: white;
  background-color: rgb(120,0,0);
}
.static_profile {
  position: fixed;
  top: 240px;
}
.static_publications {
  position: fixed;
  top: 240px;
  left: 70%;
}
.static_team {
  position: fixed;
  top: 300px;
  left: 5%;
}
.static_others {
  position: fixed;
  top: 300px;
  left: 70%;
}

.footer {
  z-index: 100;
}
.footer a {
  position: relative;
  bottom: -4px;
  left: 17%;
  margin-right: 8px;
  
}
.footer a img {
  width: 35px;
  height: 35px;
  display: inline-block;
}
.footer p {
  margin: 0;
  font-size: 18px;
  position: fixed;
  bottom: 8px;
  left: 35%;
  display: inline-block;
}

.switch {
  position: fixed;
  display: inline-block;
  bottom: 15px;
  right: 200px;
}

.switch a {
    list-style-type: none;
    text-decoration: none;
    color: white;
    padding: 4px 10px;
    border-radius: 5px 5px 5px 5px;
    min-width: 100px;
    width: 150px;
    margin: 0;
}


.deactive-tag {
  background-color: rgb(180,20,20);
}

.active-tag {
    background-color: rgb(230,20,20);
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media (max-width: 1350px) {
  .footer p {
    left: 29%;
  }
  .footer a {
    left: 190px;
  }
  .switch {
    right: 330px;
  }
}

@media (max-width: 1120px) {
  .footer a {
    left: 230px;
  }
  .switch {
    right: 300px;
  }
}
@media (max-width: 1020px) {
  .footer p {
    left: 21%;
  }
  .footer a {
    left: 180px;
  }
  .switch {
    right: 250px;
  }
}

/*mobile frame*/
@media (max-width: 930px) {
  .title-en {
    position: fixed;
    font-size: 26px;
    margin: 0;
    width: 100%;
    background-color: rgb(120,0,0);
    color: white;
    padding-top: 8px;
    padding-bottom: 8px;
    text-align: center;
    margin-top: -5px;
    height: 55px;
  }
  .title-zh {
    position: fixed;
    font-size: 34px !important;
    margin: 0;
    width: 100%;
    background-color: rgb(120,0,0);
    color: white;
    padding-top: 16px;
    padding-bottom: 8px;
    text-align: center;
    margin-top: 0px;
    height: 55px;
  }
  
  #dropdown, #seperation_line {
    display: none;
  }
  .dropdown_mobile {
    display: block;
    position: fixed;
    background-color: gray;
    color: black;
    width: 100%;
    top: 50px;
    padding-top: 3px;
    text-align: left;
    z-index: 100;
  }
  #dropdown_list_mobile {
    position: fixed;
    top: 79px;
    z-index: 50;

  }
  #dropdown_list_mobile li {
    font-family: 'Pangolin', 'ChalkboardSE-Light', sans-serif;
    padding-left: 10px;
  }
  #dropdown_mobile_current_page span {
    font-family: 'Pangolin', 'ChalkbaordSE-light' !important;
  }
  #dropdown_detail_mobile {
    position: fixed;
    top: 79px;
    background-color: gray;
    width: 100%;
    font-size: 18px;
    opacity: 1;
    z-index: 75;
  }
  #dropdown_detail_mobile li {
    font-family: 'Pangolin', 'ChalkbaordSE-light', sans-serif;
    padding-left: 70px;
  }
  .content {
    top: 90px;
    font-weight: 600;
  }
  .small {
    display: none;
  }
  .big {
    width: 85%;
  }

  .footer p{
    left: 16%;
    font-size: 16px;
    font-family: sans-serif;
  }
  .switch {
    display: none;
  }
}

@media (max-width: 715px) {
  .footer a {
    display: none;
  }
}

