}
.info-zh p{
	padding-top: 2px;
	font-size: 18px;
}
.info_pic img {
	height: 50px;
	width: 50px;
	display: block;
}

@media (max-width: 1210px) {
	.info_pic {
		left: 80px;
	}
	.info-en{
		left: 150px;
